import {eId, eClass, darkLightMode} from '../../../components/helpers/StylingHelpers';

function ParticipantsStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default ParticipantsStyle;

function setDarkLightMode(){
    darkLightMode("participants");
    darkLightMode('input');
    darkLightMode('button');
}

function setStyle(){
    if(!eId('participants')){
        return false;
    }

    eId("participants").style.height = window.innerHeight * 0.8 + 'px';

    if(window.innerWidth > window.innerHeight){
        eId('participants').style.borderRadius = parseInt(eId('participants').offsetHeight) * 0.07 + 'px';
    } else {
        eId('participants').style.borderRadius = parseInt(eId('participants').offsetWidth) * 0.07 + 'px';
    }

    let parTable = eClass('parTable')[0];
    parTable.style.height = (eId("participants").offsetTop + eId("participants").offsetHeight - parTable.offsetTop) * 0.8 + 'px';
    eClass('table')[0].style.height = 0 ;
    eClass('table')[0].style.height = (eClass('addPar')[0].offsetTop - eClass('table')[0].offsetTop) - parTable.offsetHeight * 0.03 + 'px';
    eClass('tbody')[0].style.height = eClass('table')[0].offsetHeight -  eClass('thead')[0].offsetHeight + 'px';
    setInput('input');
}

function setInput(ref){
    let input = eClass(ref);
    for(let i = 0; i < input.length; i++){
        input[i].style.borderRadius = input[0].offsetWidth * 0.05 + 'px';
    }
}